export default {
  data() {
    return {
      valid: false,
      location: '',
      personAmount: '',
      plz: '',
      rules: {
        email: (v) => !!(v || '').match(/@/) || 'Keine gültige E-Mail',
        length: (len) => (v) =>
          (v || '').length >= len ||
          `Ungültige Zeichenlänge, ${len} erforderlich`,
        required: (v) => !!v || 'Bitte ausfüllen',
      },
      dates: [
        new Date().toISOString().substr(0, 10),
        new Date().toISOString().substr(0, 10),
      ],
      modal: false,
      selected: [],
      price: '',
      checkboxes: [
        {
          label: 'Türspion',
          value: 'door spion',
        },
        {
          label: 'Barrierefrei',
          value: 'barrier free',
        },
        {
          label: 'Sprechanlage',
          value: 'door speaker',
        },
        {
          label: 'Nichtraucher',
          value: 'non-smoker',
        },
        {
          label: 'Kein Erdgeschoss',
          value: 'no ground',
        },
        {
          label: 'Internet/W-Lan',
          value: 'internet',
        },
        {
          label: 'separater Eingang',
          value: 'second door',
        },
        {
          label: 'Öffentliche Verkehrsmittel',
          value: 'public transport',
        },
      ],
      loading: false,
      showConfirmation: false,
      buttonText: 'Anfrage versenden',
      error: {
        status: false,
        message: '',
      },
    }
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' – ')
    },
  },
  methods: {
    validate() {
      if (!this.$refs.housing.validate()) return
      this.loading = true
      this.$fire.functions
        .httpsCallable('email-sendReqHousingMail')({
          message: this.message,
          place: this.location,
          zip: this.plz,
          amount: this.personAmount,
          monthprice: this.price,
          range: this.dates,
          features: this.selected,
          createdAt: new Date(),
        })
        .then(() => {
          this.buttonText = 'Versendet'
          this.loading = false
          this.showConfirmation = true
          this.valid = false
        })
        .catch((err) => {
          this.error.status = true
          this.error.message = err.message
        })
    },
  },
}
